/*
* Ensure proper alignment of the vue material icons
*/
.material-design-icon[data-v-379770a7] {
  display: flex;
  align-self: center;
  justify-self: center;
  align-items: center;
  justify-content: center;
}
.button-vue[data-v-379770a7] {
  position: relative;
  width: fit-content;
  overflow: hidden;
